import React from "react"
import Img from "gatsby-image"

function formatAuthors(arr, conjunction='and', ifempty=''){
    let l = arr.length;
    if (!l) return ifempty;
    if (l<2) return arr[0];
    if (l<3) return arr.join(` ${conjunction} `);
    arr = arr.slice();
    arr[l-1] = `${conjunction} ${arr[l-1]}`;
    return arr.join(", ");
}

const NonStretchedImage = props => {
  let normalizedProps = props
  if (props.fluid && props.fluid.presentationWidth) {
    normalizedProps = {
      ...props,
      style: {
        ...(props.style || {}),
        maxWidth: props.fluid.presentationWidth,
        margin: "0 auto", // Used to center the image
      },
    }
  }

  return <Img {...normalizedProps} />
}

const Publication = ({ title, abstract, URL, author, fields: { custom: { links, image: { src } }, issued } }) => (
    <div style={{ padding: '1rem' }}>
        <h3 className="text-underline">{title}</h3>
        <h5 className="mb-2">{formatAuthors(author.map(a => `${a.given} ${a.family}`))}</h5>
        <div>
            <NonStretchedImage fluid={src.childImageSharp.fluid} alt="" />
        </div>
        <div className="mb-2">
            {links.map(l => <a className="mr-2" href={l.URL}>[{l.name}]</a>)}
        </div>
        <div>{abstract}</div>
        <div className="text-right font-weight-light font-italic mt-2">
            {issued}
        </div>
        <hr />
    </div>
)

export default Publication

